.popup {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  inline-size: 100%;
  block-size: 100%;
  z-index: 2;
  box-sizing: border-box;
  opacity: 1;
  visibility: visible;
  transition: all 0.15s linear;
}

.popup::before {
  content: " ";
  position: absolute;
  top: 0;
  left: 0;
  inline-size: 100%;
  block-size: 100%;
  z-index: 1;
  background-color: black;
  opacity: 0.6;
}

.container {
  inline-size: 100%;
  block-size: 100%;
  max-inline-size: 720px;
  max-block-size: 718px;
  margin: auto;
  position: relative;
  z-index: 3;
  background: #1c1c21;
  border: 1px solid rgba(76, 76, 255, 0.2);
  box-shadow: 0px 24px 32px rgba(0, 0, 0, 0.04),
    0px 16px 24px rgba(0, 0, 0, 0.04), 0px 4px 8px rgba(0, 0, 0, 0.04),
    0px 0px 1px rgba(0, 0, 0, 0.04);
  border-radius: 40px;
  box-sizing: border-box;
  padding: 120px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.close {
  outline: 0;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 0;
  border-radius: 0;
  padding: 0;
  inline-size: 24px;
  block-size: 24px;
  background-color: transparent;
  background-image: url(../../images/popup/btn_close.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 60px;
  right: 40px;
  opacity: 1;
  transition: opacity 0.1s linear;
}

.close:hover {
  opacity: 0.6;
  cursor: pointer;
}

.icon_complete {
  inline-size: 120px;
  block-size: 120px;
  background-image: url(../../images/popup/icon_complete.svg);
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 107px 102px;
}
