.image {
  inline-size: 240px;
  block-size: 120px;
  object-fit: contain;
}

.cost {
  display: flex;
}

.priceNumber {
  padding-inline-end: 9px;
}

.description {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
  color: #f2f2f3;
  width: 100%;
  height: 100%;
}

.counter {
  position: absolute;
  top: 0;
  right: 0;
  width: 32px;
  height: 32px;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.image:hover {
  cursor: pointer;
  transform: scale(1.1);
  transition: transform 0.15s linear;
}
